screen */\n.error-message {\n  font-size: 0.7em;\n  background-color: var(--light-shadow);\n  padding: 1em;\n  color: var(--lighter-red);\n  max-height: 33px;\n  border-left: solid 2px;\n  border-radius: 2px;\n}\n\n/* Success message in the Login screen */\n.success-message {\n\tfont-size: 0.7em;\n\tbackground-color: var(--light-shadow);\n\tpadding: 1em;\n\tcolor: var(--lighter-green);\n\tmax-height: 33px;\n\tborder-left: solid 2px;\n\tborder-radius: 2px;\n  }\n\n/* user logo related */\n.user-info {\n  /*grid-column: 6;*/\n  position: relative;\n  justify-self: end;\n}\n\n.user-icon {\n  border-radius: 50px;\n  width: 30px;\n  height: 30px;\n\n  position: absolute;\n  top: -6px;\n  left: -35px;\n  border: solid 1px var(--dark-grey);\n  text-transform: uppercase;\n  padding: 0.2em;\n  font-size: 1em;\n}\n\n.user-greeting {\n  display: block;\n}\n\n.user-greeting:hover {\n  cursor: pointer;\n}\n\n/* drop down menu */\n.user-info > .drop-down {\n  display: none;\n}\n\n.user-info:hover > .drop-down {\n  display: block;\n}\n\n.drop-down {\n  padding: 1em;\n  position: absolute;\n}\n\n/* Log out div */\n.logout {\n  font-size: 0.7em;\n  padding: 1em;\n  position: absolute;\n  right: 0;\n  top: 23px;\n  color: var(--light-purple);\n  background-color: var(--lightest-grey);\n  z-index: 1;\n  border-radius: 2px;\n}\n\n.logout::after {\n  content: \"\";\n  border-right: solid 5px transparent;\n  border-bottom: solid 5px var(--lightest-grey);\n  border-left: solid 5px transparent;\n  position: absolute;\n  top: -5px;\n  right: 8px;\n  width: 0;\n  height: 0;\n}\n\n.logout-link {\n  text-decoration: none;\n  color: var(--light-purple);\n  padding-left: 0.5em;\n  text-align: left;\n  padding-right: 0.5em;\n  background-color: var(--lightest-grey);\n}\n\n.logout-link:hover,\n.logout-link:focus {\n  opacity: 0.6;\n}\n\n/* Rooms panel related styles */\n.rooms-panel {\n  overflow-x: hidden;\n  overflow-y: auto;\n}\n\n.room-info {\n  position: relative;\n  display: grid;\n  grid-template-columns: 1fr 5fr 1fr;\n  margin-bottom: 10px;\n}\n\n.room-initials {\n  border-radius: 50px;\n  border: solid 1px var(--dark-grey);\n  align-self: center;\n  justify-self: center;\n  padding: 0.5em;\n  text-transform: uppercase;\n  position: relative;\n}\n\n.online-mark {\n  background-color: var(--light-purple);\n  width: 5px;\n  height: 5px;\n  border-radius: 50%;\n  position: absolute;\n  top: 0px;\n  right: 5px;\n}\n\n.active-room {\n  background-color: var(--lightest-purple);\n}\n\n.room-info:hover,\n.room-info:focus {\n  background-color: var(--lightest-purple);\n  cursor: pointer;\n}\n\n.room-icon-div {\n  justify-self: center;\n  display: grid;\n}\n\n.room-icon {\n  height: 50px;\n  width: 50px;\n  border-radius: 50px;\n  padding: 4px;\n}\n\n.room-name {\n  font-size: 0.9em;\n  padding: 0.6em;\n  color: var(--darker-grey);\n}\n\n.last-message {\n  font-size: 0.75em;\n  color: var(--lightest-black);\n  text-overflow: ellipsis;\n  overflow: hidden;\n  width: 100px;\n}\n\n.unread-msg {\n  font-weight: bold;\n}\n\n.date-info {\n  font-size: 0.65em;\n  padding: 0.9em;\n  color: var(--lighter-black);\n  text-align: right;\n}\n\n/* message panel related */\n.message-panel {\n  background-color: var(--lighter-grey);\n  display: grid;\n  grid-template-rows: auto 80px;\n  overflow-y: hidden;\n  overflow-x: hidden;\n}\n\n.show-messages {\n  position: relative;\n  display: grid;\n  overflow-y: auto;\n}\n\n.msg {\n  font-size: 0.7em;\n  text-align: left;\n  border: 1px solid var(--lighter-grey); /*#f9f9f9;*/\n  border-radius: 5px;\n  padding: 1.5em;\n  margin: 1em;\n\n  position: relative;\n  word-wrap: break-word;\n  max-height: 100px;\n  white-space: pre-wrap;\n}\n\n.room-msg {\n  background-color: var(--lightest-white);\n  justify-self: start;\n}\n\n.room-msg::before {\n  content: \"\";\n  /* border-top: solid -4px transparent; */\n  border-right: solid 20px var(--lightest-white);\n  border-bottom: solid 28px transparent;\n  position: absolute;\n  left: -12px;\n  width: 0;\n  height: 0;\n  top: 8px;\n}\n\n.my-msg {\n  background-color: var(--lighter-purple);\n  justify-self: end;\n}\n\n.my-msg::after {\n  content: \"\";\n  /* border-top: solid -4px transparent; */\n  border-left: solid 20px var(--lighter-purple);\n  border-bottom: solid 28px transparent;\n  position: absolute;\n  right: -12px;\n  width: 0;\n  height: 0;\n  top: 8px;\n}\n\n.time-sent {\n  position: absolute;\n  bottom: -7px;\n  right: 0;\n  padding: 1em;\n  font-size: 0.8em;\n  color: var(--lightest-black); /*#797676;*/\n}\n\n\n\n\n\n\n\n\n.chat-input-container {\n\tdisplay: flex;\n\talign-items: center;  /* Centers the elements vertically */\n\tgap: 10px;           /* Adds some space between the file input and the textarea */\n}\n\n.file-upload-button {\n\tpadding: 0.5em 1em;\n\tbackground-color: var(--upload-file-button);  /* Adjust the color to fit your theme */\n\tcolor: white;\n\tborder: none;\n\tborder-radius: 4px;\n\tcursor: pointer;\n\tfont-size: 0.8em;\n\talign-self: end;  /* Aligns with the bottom of the textarea */\n\tbox-shadow: 0 0 3px var(--light-shadow);\n\ttransition: background-color 0.3s ease;\n  }\n\n.file-upload-button:hover,\n.file-upload-button:focus {\n  background-color: var(--button-hover-color);  /* Lighter or darker depending on your theme */\n}\n\n.file-upload-input {\n\tdisplay: none;  /* Keeps the input hidden */\n}\n\n/* message writing div related styles\n.msg-write-div {\n  align-self: end;\n  padding: 1em;\n  font-size: 0.8em;\n  font-family: inherit;\n\n  border: 1px solid var(--light-purple);\n  border-radius: 2px;\n\n  overflow-y: hidden;\n  height: 77px;\n} */\n\n.msg-write-div {\n\tflex-grow: 1;\n\tpadding: 1em;\n\tfont-size: 0.8em;\n\tfont-family: inherit;\n\tborder: 1px solid var(--light-purple);\n\tborder-radius: 2px;\n\toverflow-y: hidden;\n\theight: 77px;\n  }\n\n\n.msg-write-div:hover,\n.msg-write-div:focus {\n  /*border: 2px solid #ab14ab;*/\n  box-shadow: 0 0 3px var(--light-shadow);\n}\n\ntextarea {\n  width: 100%;\n  resize: none;\n  border: 0;\n  outline: 0;\n}\n\n\n\n\n\n\n\n\n\n\n\n\n\n/* Loading image related */\n.loading-div {\n  display: grid;\n  align-items: center;\n  justify-items: center;\n}\n\n.loading-gif {\n  width: 100px;\n}\n\n/* Hide a div */\n.hide-div {\n  display: none;\n}\n\n/* media queries */\n@media screen and (max-width: 500px) {\n  body {\n    font-size: var(--font-small);\n  }\n\n  .content {\n    grid-template-columns: 1fr;\n  }\n\n  .my-msg::after {\n    right: -8px;\n    top: 8px;\n    border-left: solid 11px var(--lighter-purple);\n    border-bottom: solid 11px transparent;\n  }\n\n  .room-msg::before {\n    left: -7px;\n    top: 8px;\n    border-right: solid 11px var(--lightest-white);\n    border-bottom: solid 11px transparent;\n  }\n\n  .time-sent {\n    bottom: -4px;\n    right: 0;\n  }\n\n  .online-mark {\n    top: 0px;\n    right: 1px;\n  }\n}\n"],"sourceRoot":""}]);
